"use client"
import React, { useEffect, useState } from 'react';
import { Box, Button, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper } from '@mui/material';
import Image from 'next/image'; // Si usas Next.js, si no, usa 'img' directamente
import { supabase } from '../../../utils/supabaseClient'; // Importa el cliente de Supabase

const ProductTable = () => {
  const [products, setProducts] = useState([]); // Para almacenar los productos
  const [loading, setLoading] = useState(true); // Para mostrar un estado de carga

  // Cargar los productos desde Supabase
  const fetchProducts = async () => {
    try {
      setLoading(true);
      const { data, error } = await supabase.from('products').select('*'); // Reemplaza 'products' con el nombre de tu tabla

      if (error) {
        throw error;
      }

      setProducts(data); // Guarda los productos en el estado
    } catch (error) {
      console.error('Error al obtener los productos:', error);
    } finally {
      setLoading(false);
    }
  };

  // Cargar los productos al montar el componente
  useEffect(() => {
    fetchProducts();
  }, []);

  return (
    <Box mt={4} className="p-6">
      <Button
        variant="contained"
        href="newProduct"
        sx={{
          backgroundColor: '#1e90ff',
          color: 'white',
          '&:hover': {
            backgroundColor: '#187bcd',
          },
        }}
        size="large"
      >
        Añadir Nuevo Producto
      </Button>

      {/* Tabla de productos */}
      <TableContainer component={Paper} sx={{ mt: 3 }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Imagen</TableCell>
              <TableCell>Producto</TableCell>
              <TableCell>Descripción</TableCell>
              <TableCell>Precio</TableCell>
              <TableCell>Categoría</TableCell>
              <TableCell></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {loading ? (
              <TableRow>
                <TableCell colSpan={6} align="center">Cargando...</TableCell>
              </TableRow>
            ) : (
              products.map((product) => (
                <TableRow key={product.id}>
                  <TableCell>
                    <Image
                      src={product.image_url} // Asumiendo que 'image_url' es la columna en tu tabla que almacena la URL de la imagen
                      alt={product.name}
                      width={50}
                      height={50}
                    />
                  </TableCell>
                  <TableCell>{product.name}</TableCell>
                  <TableCell>{product.description}</TableCell>
                  <TableCell>${product.price}</TableCell>
                  <TableCell>{product.category}</TableCell>
                  <TableCell>
                    <Button>Editar</Button>
                  </TableCell>
                </TableRow>
              ))
            )}
          </TableBody>
        </Table>
      </TableContainer>
    </Box>
  );
};

export default ProductTable;
